<template>
  <div>
    <slick
      class="study-slider js-study-slider"
      ref="slick"
      :options="slickOptions"
      >
      <div class="study-slider__item" v-for="(image, index) in images" v-bind:key="index">     
          <a class="study-slider__link" href="#">
            <img 
              v-lazy="image.src"
              @click="openGallery(index)"
            >
          </a>
      </div>
    </slick>
    <LightBox
      :images="images" 
      ref="lightbox"
      :show-caption="false"
      :show-light-box="false"
      :show-thumbs="false"
    ></LightBox>
  </div>
</template>

<script> 
import Slick from 'vue-slick';
import LightBox from 'vue-image-lightbox';
export default {
  name: 'imgSlider',
  components: { 
    Slick,
    LightBox
  },
  data() {
    return {
      images:[
        {
          thumb: require('@/assets/img/certificate-1.jpg'),
          src: require('@/assets/img/certificate-1.jpg'),
          caption: '<h4>JavaScript базовий курс</h4>',
        },
        {
          thumb: require('@/assets/img/certificate-2.jpg'),
          src: require('@/assets/img/certificate-2.jpg'),
          caption: '<h4>JavaScript просунитий курс</h4>',
        },
        {
          thumb: require('@/assets/img/certificate-3.jpg'),
          src: require('@/assets/img/certificate-3.jpg'),
          caption: '<h4>Професія Веб-разробник в LOFTSCHOLL</h4>',
        },
        {
          thumb: require('@/assets/img/certificate-4.jpg'),
          src: require('@/assets/img/certificate-4.jpg'),
          caption: '<h4>Нагорода за друге місцу</h4>',
        },
        {
          thumb: require('@/assets/img/certificate-5.jpg'),
          src: require('@/assets/img/certificate-5.jpg'),
          caption: '<h4>Нагорода за друге місцу</h4>',
        }
      ],
      slickOptions: {
        dots: false,
        infinite: false,
        speed: 300,
        slidesToShow:2,
        vertical: true,
        slidesToScroll: 1,
        prevArrow: '<button type="button" data-role="none" class="slick-prev" aria-label="Previous" tabindex="0" role="button"><i class="icon-arrow"></i></button>',
        nextArrow: '<button type="button" data-role="none" class="slick-next" aria-label="Next" tabindex="0" role="button"><i class="icon-arrow-bottom"></i></button>',
        responsive: [{
          breakpoint: 1023,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            infinite: true,
            vertical: false,
            dots: false
          }
				}			
			]

      }
    };
  },
  methods: {
    openGallery(index) {
      this.$refs.lightbox.showImage(index)
    }
  }
}
</script>