<template>
  <div id="app" class="main-wrapper loaded">
    <full-page ref="fullpage" :options="options" id="fullpage">
      <MainSection />
      <AboutSection />      
      <TecnhologySection />      
      <PaintSection />			
      <StudySection />			
      <PortfolioSection />			
      <!--<ReviewsSection />-->			
      <ContactSection />
    </full-page>
    <transition
      v-on:enter="fadeIn" 
      v-on:leave="fadeOut"
    >
      <Nav
        v-if="drawer"
        @changeDrawer="onDrawerChange"
      />
    </transition>
    
    <Pagination />
    <HeaderPage
      :drawer="drawer"
      :class="{ 'show-nav': drawer }"
      @changeDrawer="onDrawerChange"
    /> 
    <Loader/>
  </div>
</template>

<script>
import HeaderPage from './components/HeaderPage'
import Pagination from './components/Pagination'
import Loader from './components/loader'
import Nav from './components/Nav'
import MainSection from './section/main'
import AboutSection from './section/about'
import TecnhologySection from './section/tecnhology'
import PaintSection from './section/paint'
import StudySection from './section/study'
import PortfolioSection from './section/portfolio'
import ContactSection from './section/contact'
import { mapMutations,mapState } from 'vuex'
import { TimelineMax, Power4 } from 'gsap'

export default {
  name: 'app',
  components: {
    HeaderPage,
    Nav,
    Pagination,
    MainSection,
    AboutSection,
    TecnhologySection,
    PaintSection,
    StudySection,
    PortfolioSection,
    ContactSection,
    Loader
  },
  data() {
    return {
      drawer: false,
      windowWidth: 0,
      windowHeight: 0,
      options: {
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        anchors: ['main', 'about', 'tecnhology', 'paint', 'study', 'portfolio', 'reviews', 'contact'],
        menu: '#js-menu',
        scrollingSpeed: 400,
        navigation: true,
      },
    }
  },
  computed: {...mapState(['loaded'])},
  methods: {
    ...mapMutations(['setLoaded']),


    onDrawerChange (event) {      
      this.drawer = event
    },
    fadeIn(el, done) {

      const tl = new TimelineMax({
        onComplete: done
      })
      tl.set('.main-nav', { x: window.innerWidth * 1.3, rotate: -14, scale: 1.5, transformOrigin: '50% 50%'})
      tl.to('.main-nav', 0.5, { x: 0, ease: Power4.easeOut})
      tl.to('.main-nav', 1, { rotate: 0, scale: 1, ease: Power4.easeOut})
      .fromTo('.main-nav-list', 1, { autoAlpha: 0, scale: 0 }, { autoAlpha: 1, scale: 1, ease: Power4.easeOut, onComplete: done });
    },
  
    fadeOut(el, done) {
      const tl = new TimelineMax({
        onComplete: done
      })
      tl.to('.main-nav-list', 1, { autoAlpha: 0,   ease: Power4.easeOut })
        .to('.main-nav-list', 1, { scale: 0, ease: Power4.easeOut }, '-=0.7')
        .to('.main-nav', 1, { rotate: -14, scale: 1.5, autoAlpha: 0, ease: Power4.easeOut }, '-=0.9')
    },
  },
  mounted () {    
    this.setLoaded(true);
    setTimeout(function() {
      document.getElementById("app").classList.add('web');
    }, 3500);
    setTimeout(function() {
      document.getElementById("app").classList.add('load');
      document.getElementById("app").classList.remove('loaded');      
    }, 5000);
  },
}
</script>

<style lang="scss" src="@/assets/css/index.scss" />