export default {
  namespaced: false,
  state: {    
    skills: [{
      id: 1,
      title: 'Обучение'
    }]
  },
  getters: {
    getSkills: state => state.skills,
    skillsById (state) {
      return skillsId => {
        return state.skills.find(skills => skills.id === skillsId)
      }
    }
  },
  actions: {

  },
  mutations: {

  }
}
