<template>
  <div class="section section-contact" data-anchor="contact">
    <div class="section-bg"></div>
    <div class="wrapper">
      <div class="contacts-row">
        <div class="contacts-inf">
          <h2>Контакти</h2>
          <ul class="social-list">
            
            <li class="social-list__item">
              <a href="https://www.linkedin.com/in/koljamoiseiev/" target="_blanck" class="social-list__link">
                <i class="icon-linkedin"></i>
              </a>
            </li>
            <li class="social-list__item">
              <a href="https://www.instagram.com/koljamoiseiev/" target="_blanck" class="social-list__link">
                <i class="icon-inst"></i>
              </a>
            </li>
            <li class="social-list__item">
              <a href="https://www.facebook.com/profile.php?id=100017334272832&ref=bookmarks" target="_blanck" class="social-list__link">
                <i class="icon-fb"></i>
              </a>
            </li>
            <li class="social-list__item">
              <a href="https://github.com/MokusM" target="_blanck" class="social-list__link">
                <i class="icon-git"></i>
              </a>
            </li>
          </ul>
          <ul class="contact-inf">
            <li class="contact-inf__item">
              <a href="tel:380631522267" class="contact-inf__link">
                <i class="icon-phone"></i>
                <span>+38-(063)-152-22-67</span>
              </a>
            </li>
            <li class="contact-inf__item">
              <a href="mailto:moceevmukola@gmail.com" class="contact-inf__link">
                <i class="icon-mail"></i>
                <span>moceevmukola@gmail.com</span>
              </a>
            </li>
            <li class="contact-inf__item">
              <a href="mailto:moceevmukola@gmail.com" class="contact-inf__link">
                <i class="icon-pdf"></i>
                <span>Скачати резюме</span>
              </a>
            </li>
          </ul>
        </div>
        <div class="contacts-form" @mouseover="logoAnimation">
          <div class="contacts-form__logo">            
            <svg xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 299.5 306.6"
        role="presentation"
        x="0px" y="0px"
	      style="enable-background:new 0 0 299.5 306.6;"
        xml:space="preserve"
      >

        <polygon fill="#FFFFFF" class="logo-top" points="110.8,90.8 0,1.3 0.1,217.2 30.7,195.3 30.9,65.9 111,130.7 222,41 222,1.3 "/>
        <polygon fill="#FFFFFF" class="logo-bottom" points="191.3,105.3 191.1,234.8 111.1,169.9 0,259.7 0,299.3 111.2,209.8 222,299.3 221.9,83.5 "/>
      </svg>
          </div> 
          
          <h2>зворотній зв’язок</h2>
          <form  v-if="!sendMessage" @submit.prevent="submitHandler">
            <div class="box-field">
              <div 
                class="box-field__input"
                :class="{invalid: $v.name.$dirty && !$v.name.required}"
              >
                <input
                  type="text"
                  placeholder="Ім’я"
                  class="form-control"
                  v-model.trim="name"
                  
                />                
              </div>
              <small
                class="helper-text invalid"
                v-if="$v.name.$dirty && !$v.name.required"
              >Поле Ім’я не повинно бути пустим</small>
            </div>
            <div class="box-field">
              <div
                class="box-field__input"
                :class="{invalid: $v.phone.$dirty && !$v.phone.required}"
              >
                <input
                  type="text"
                  placeholder="Телефон або Email"
                  class="form-control"
                  v-model.trim="phone"                  
                />                
              </div>
              <small
                class="helper-text invalid"
                v-if="$v.phone.$dirty && !$v.phone.required"
              >Поле не повинно бути пустим</small>
            </div>
            <div class="box-field">
              <div
                class="box-field__input"
                :class="{invalid: $v.phone.$dirty && !$v.phone.required}"
              >
                <textarea
                  type="text"
                  placeholder="Повідомлення"
                  class="form-control"
                  v-model.trim="message"  
                >
                </textarea>  
              </div>
              <small
                class="helper-text invalid"
                v-if="$v.message.$dirty && !$v.message.required"
              >Поле не повинно бути пустим</small>
            </div>
            <div class="text-right">
              <button type="submit" class="btn-send">Відправити</button>
            </div>            
          </form>
          <div v-else class="email-send">
              <div class="email-send__title">Email відправлено</div>
              <a href="#" class="email-send__link" @click.stop="sendMessage = false">Відправити ще раз</a>  
             
            </div> 
          
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { required } from 'vuelidate/lib/validators'
import { TimelineLite, Power4} from "gsap"
export default {
  name: "contact",
  data: () => ({
    name: '',
    message: '',
    phone: '',
    sendMessage: false,
  }),
  validations: {
    message: { required },
    name: { required },
    phone: { required },
  },
  methods: {
    async submitHandler () {      
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      const formData = {
        message: this.message,
        name: this.name,
        phone: this.phone,
      }
      this.sendMessage = true
      try {
        console.log(formData);
      } catch (e) {}
    },
    
    logoAnimation() {

      const tl = new TimelineLite()
      tl.to('.contacts-form__logo', 0.4, {scale:1.2, ease: Power4.easeOut})
      tl.to('.contacts-form__logo', 0.2, {scale:1, delay:0.5})
    },
  }
};
</script>